import {useState} from 'react';
import '../styles/calculator.css'
import { Link } from "react-router-dom";
import  CloseIcon  from '@material-ui/icons/Close'
import BackspaceOutlinedIcon from '@material-ui/icons/BackspaceOutlined';


function Calc(){
    const [result, setResult] = useState("");

    const handleClick = (e) => {
        
        setResult(result.concat(e.target.name));
      }

    const calculate = () => {
        try{
        setResult(eval(result).toString())

        }
        catch(err){
            setResult('Error')
        }
    }

    const clearAll = () => {
        setResult('');
    }

    const backspace = () => {
        setResult(result.slice(0, result.length-1));
    }


    return(
        <div>
            <div className="d-flex justify-content-end me-3" >
                <Link to='/projects'><CloseIcon style={{fontSize:'50px', color:'red'}} /></Link>
            </div>
            <h2 className='mb-2 text-center'>SIMPLE CALCULATOR</h2>
        <div className='calculator d-grid'>
            
            <div className='calcbox w-100'>
                <input className='form-control inputForCalc form-control-lg text-end ' type='text' value={result}></input><br/>
                <div className='ipbuttons' >
                <div className='row ' >
                    <div class="col-6"><button className='bwidth btn btn-warning ' onClick={clearAll}>AC</button></div>
                    <div class="col-3"><button className='iconbutton btn press  w-100 ' onClick={backspace}><BackspaceOutlinedIcon /></button></div>
                    <div class='col-3'><button className='iconbutton btn press  w-100 ' name='/' onClick={handleClick}>&divide;</button></div>
                </div>
                <div className='row mt-1' >
                    <div class="col-3"><button className='btn press w-100 ' name='7' onClick={handleClick}>7</button></div>
                    <div class="col-3"><button className='btn press w-100 ' name='8' onClick={handleClick}>8</button></div>
                    <div class='col-3'><button className='btn press w-100 ' name='9' onClick={handleClick}>9</button></div>
                    <div class='col-3'><button className='btn press w-100 ' name='*' onClick={handleClick}>&times;</button></div>
                </div>

                <div className='row mt-1' >
                    <div class="col-3"><button className='btn  press w-100 ' name='4' onClick={handleClick}>4</button></div>
                    <div class="col-3"><button className='btn press w-100 ' name='5' onClick={handleClick}>5</button></div>
                    <div class='col-3'><button className='btn press w-100 ' name='6' onClick={handleClick}>6</button></div>
                    <div class='col-3'><button className='btn press  w-100 ' name='-' onClick={handleClick}>-</button></div>
                </div>

                <div className='row mt-1' >
                    <div class="col-3"><button className='btn press w-100 ' name='1' onClick={handleClick}>1</button></div>
                    <div class="col-3"><button className='btn press w-100 ' name='2' onClick={handleClick}>2</button></div>
                    <div class='col-3'><button className='btn press  w-100 ' name='3' onClick={handleClick}>3</button></div>
                    <div class='col-3'><button className='btn press w-100 ' name='+' onClick={handleClick}>+</button></div>
                </div>

                <div className='row mt-1' >
                    <div class="col-3"><button className='btn press w-100 ' name='0' onClick={handleClick}>0</button></div>
                    <div class="col-3"><button className='btn press w-100 ' name='.' onClick={handleClick}>.</button></div>
                    <div class='col-6'><button className='bwidth btn btn-warning' onClick={calculate}>=</button></div>
                </div>  



            </div>
            </div>
        </div>
    </div>
    )
}

export default Calc;